const DetailMyList = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [isLoading, setIsLoading] = useState(true); //true로 바꿀것
  const [data, setData] = useState(null);
  const [writerId, setWriterId] = useState(null);
  const detailId = searchParams.get("wishList_id");
  const { naver } = window;
  const [userId, setUserId] = useState("");
  const [jwt, setJwt] = useState("");
  const [isApp, setIsApp] = useState(null);
  const [platform, setPlatform] = useState(null);

  const [map, setMap] = useState([]);
  const [mapRender, setMapRender] = useState(false);
  const [markerRender, setMarkerRender] = useState(false);
  const markerArr = useRef([]);

  const addrReplace = (place) => {
    if (place !== undefined) {
      const splitAddr = place.split(" ");
      const Addr = splitAddr.slice(0, 2).join(" ");
      return Addr;
    }
  };

  const getData = async () => {
    setIsLoading(true);
    await axios({
      method: "get",
      headers: {
        "Access-Control-Allow-Credentials": true,
        "ngrok-skip-browser-warning": true,
      },
      url: `${process.env.REACT_APP_API_URL}/wishList/wishDetail1?wishList_id=${detailId}`,
    })
      .then((res) => {
        if (res.data.isPass) {
          // console.log('gigigi',res.data);
          // alert(JSON.stringify(res.data.wishDetailList.data));
          setData(res.data.wishDetailList.data);
          setWriterId(res.data.wishDetailList.userId);
          setIsLoading(false);
          const sendData = {
            name: "listShare",
            listId: res.data.wishDetailList.listId,
            listTitle: res.data.wishDetailList.listName,
            image: res.data.wishDetailList.data[0].thumbnail,
          };
          // console.log(sendData);
          window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
        }
      })
      .catch((err) => {
        console.log(err);
        setIsLoading(false);
      });
  };
  useEffect(() => {
    window.ReactNativeWebView?.postMessage(JSON.stringify({ mount: true }));
    //android
    document.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        if (parseData.loginData) {
          setUserId(parseData.loginData.id);
          setJwt(parseData.loginData.jwt);
        }
        setIsApp(parseData.isApp);
        setPlatform(parseData.platform);
      } catch (err) {
        console.log(err);
      }
    });
    //ios
    window.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        if (parseData.loginData) {
          setUserId(parseData.loginData.id);
          setJwt(parseData.loginData.jwt);
        }
        setIsApp(parseData.isApp);
        setPlatform(parseData.platform);
      } catch (err) {
        console.log(err);
      }
    });
    getData();
    // 카카오톡 sdk 추가
    const script = document.createElement("script");
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    script.async = true;
    document.body.appendChild(script);
    return () => document.body.removeChild(script);
  }, []);

  useEffect(() => {
    if (data?.length > 0 && !mapRender) {
      const firstMap = new naver.maps.Map("map", {
        center: new naver.maps.LatLng(data[0].mapy, data[0].mapx),
        zoomControl: false,
        zoom: 7,
      });
      setMap(firstMap);
      setMapRender(true);
    }
  }, [data]);

  useEffect(() => {
    if (data?.length > 0 && mapRender && !markerRender) {
      // const map = new naver.maps.Map("map", {
      //   center: new naver.maps.LatLng(data[0].mapy, data[0].mapx),
      //   zoomControl: false,
      //   zoom: 7,
      // });
      // if (markerArr.current[0]) {
      //   markerArr.current.forEach((e) => {
      //     e.setMap(null);
      //   });
      //   // markerList 배열 초기화
      //   markerArr.current.splice(0);
      // }

      for (let i = 0; i < data.length; i++) {
        const markers = new naver.maps.Marker({
          position: new naver.maps.LatLng(data[i].mapy, data[i].mapx),
          map,
          // 원하는 이미지로 마커 커스텀
          icon: {
            url: data[i].markerImg,
            size: new naver.maps.Size(30, 30),
            scaledSize: new naver.maps.Size(30, 30),
            origin: new naver.maps.Point(0, 0),
            // anchor: new naver.maps.Point(25, 26),
          },
        });
        // const infoWindow = new naver.maps.InfoWindow({
        //   content: `<div style="width:auto; text-align:center; font-size:75%; padding:10px;"><b>${data[i].title}</b><br /><p>${data[i].addr}</p></div>`,
        // });
        // markers.addListener("click", () => {
        //   if (infoWindow.getMap()) {
        //     infoWindow.close();
        //   } else {
        //     infoWindow.open(map, markers);
        //   }
        // });
        markerArr.current.push(markers);
        setMarkerRender(true);
      }
    }
  }, [data, mapRender]);

  const deleteEvent = async (contentid, index) => {
    if (window.confirm("장소를 삭제하시겠습니까?")) {
      await axios({
        method: "post",
        headers: {
          "Access-Control-Allow-Credentials": true,
          "ngrok-skip-browser-warning": true,
          Authorization: jwt,
        },
        url: `${process.env.REACT_APP_API_URL}/wishList/deletePlace`,
        data: {
          wishList_id: detailId,
          contentid: contentid,
        },
      })
        .then((res) => {
          // alert(JSON.stringify(res.data));
          if (res.data.isPass) {
            const copyArr = [...data];
            const result = copyArr.filter((el) => el.id !== contentid);
            setData(result);
            markerArr[index].setMap(null);
            // window.location.reload();
            alert("삭제되었습니다.");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    } else {
      return;
    }
  };

  const extractFirstTwoLetters = (address) => {
    return address.substring(0, 2);
  };

  const kakaoShareEvent = (event, el) => {
    event.stopPropagation();
    if (isApp) {
      const sendData = {
        name: "kakaoShare",
        contentId: el.id,
        typeId: el.contenttypeid,
        title: el.title,
        image: el.thumbnail,
        address: el.addr,
      };
      window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
    } else {
      if (window.Kakao) {
        const kakao = window.Kakao;

        if (!kakao.isInitialized()) {
          // javascript key
          kakao.init("69c873f1cf4e580ab99c761390d9777e");
        }
        kakao.Link.sendDefault({
          objectType: "feed",
          content: {
            title: "Chat GPT 기반 당일치기 여행추천 앱 여행대로",
            description: `${extractFirstTwoLetters(el.addr)}의 ${el.title}을 추천합니다`,
            imageUrl: `${el.thumbnail}`,
            imageWidth: 400,
            imageHeight: 300,
            link: {
              mobileWebUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.id}`,
              webUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.id}`,
            },
          },
          buttons: [
            {
              title: "추천 여행지 확인",
              link: {
                // 기본 도메인 설정값으로 가짐 (링크를 뭘 주던) 수정
                mobileWebUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.id}`,
                webUrl: `${process.env.REACT_APP_WEB_URL}/placedetail/${el.id}`,
              },
            },
          ],
        });
      }
    }
  };

  const movePlaceDetail = (el) => {
    alert(el.id);
    if (isApp) {
      const sendData = {
        name: "movePlaceDetail",
        contentId: el.id,
        title: el.title,
      };
      window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
    } else {
      if (window.confirm("장소정보를 확인하시려면 여행대로 앱 다운로드가 필요합니다.")) {
        if (platform === "ios") {
          window.open(
            "https://apps.apple.com/kr/app/%EC%97%AC%ED%96%89%EB%8C%80%EB%A1%9C-%EB%8B%B9%EC%9D%BC%EC%B9%98%EA%B8%B0-%EC%97%AC%ED%96%89%EA%B3%BC-nft-%EC%A0%80%EC%9E%A5/id6458978344",
            "_blank"
          );
        } else {
          window.open(
            "https://play.google.com/store/apps/details?id=com.datatourapp&hl=ko-KR",
            "_blank"
          );
        }
      } else {
        return;
      }
    }
  };

  return (
    <Container>
      {isLoading ? (
        <LoadingBox>
          <ClipLoader color="#25B1FA" />
        </LoadingBox>
      ) : data?.length <= 0 ? (
        <LoadingBox>
          <p>장소목록이 없습니다.</p>
        </LoadingBox>
      ) : (
        <>
          <MapContainer>
            <div id="map" style={{ width: "100%", height: "100%", display: "flex", flex: 1 }} />
          </MapContainer>
          <ListContainer>
            {data?.map((el, idx) => {
              return (
                <div key={idx}>
                  <ItemListConatiner onClick={() => movePlaceDetail(el)}>
                    <LeftImageContainer>
                      <ItemImage src={el.thumbnail} />
                    </LeftImageContainer>
                    <RightItemContainer>
                      <div>
                        <ItemPlaceName>{el.title}</ItemPlaceName>
                        <ItemPlaceInfoContainer>
                          <ItemInfoText>리뷰 수({el.reviewCount})</ItemInfoText>
                          {/* <ItemInfoText style={{ marginLeft: 4 }}>평점({el.grade / 10})</ItemInfoText> */}
                          <ItemInfoText style={{ marginLeft: 4 }}>
                            평점({el.grade / 10})
                          </ItemInfoText>
                        </ItemPlaceInfoContainer>
                      </div>
                      <BottomItemContainer>
                        <BottomItemAddrContainer>
                          <BottomItemAddrText>{addrReplace(el.addr)}</BottomItemAddrText>
                        </BottomItemAddrContainer>
                        <BottomItemBtnContainer>
                          <BtnImage
                            style={{
                              marginRight: 8,
                              display: userId === writerId ? "flex" : "none",
                            }}
                            src={deleteIcon}
                            onClick={(event) => {
                              event.stopPropagation(); // 이벤트 전파 중지
                              deleteEvent(el.id, idx);
                            }}
                          />
                          <BtnImage
                            src={shareBtn}
                            onClick={(event) => kakaoShareEvent(event, el)}
                          />
                        </BottomItemBtnContainer>
                      </BottomItemContainer>
                    </RightItemContainer>
                  </ItemListConatiner>
                  <ItemListLine />
                </div>
              );
            })}
          </ListContainer>
        </>
      )}
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* padding: 14px 20px 50px 20px; */
  padding-bottom: 100px;
`;
